  import Scroll from 'base/scroll/scroll'
   import { mapGetters } from 'vuex'
   import { listMyOrder } from 'api/api'
  export default {
    data() {
      return {
        orderList: [],
        currentIndex: 0,
        menuList: [{'name':'全部'},{'name':'代付款'},{'name': '代发货'},{'name': '待收货'},{'name': '待评价'}]
      }
    },
     computed: {
    ...mapGetters([
			'userAccount'
		])
   },
   created() {
     this._getListMyOrder()
   },
    methods: {
      back() {
        this.$router.back()
      },
      selectTabs(index,item) {
			  this.currentIndex = index
		  },
      _getListMyOrder() {
        const toast = this.$createToast({
          time: 0,
          txt: '数据加载中...'
        })
        toast.show()
        let uid = this.userAccount.uid
        let token = this.userAccount.token
        let status = 1
        let page = 0
        listMyOrder(uid,token,status,page).then((res)=>{
          // console.log(res)
          if(res.status === 1) {
            this.orderList = res.data
            toast.hide()
          }
          
        })
      }
    },
    components: {
      Scroll
    }
  }



